.nav {
    padding: 40px 0;
    &__row {
        display: flex;
        align-items: center; }
    &__logo {
        margin-right: 83px;
        font-size: 28px;
        &:hover {
            text-decoration: underline;
            cursor: pointer; } }
    &__list {
        padding: 0;
        margin: 0;
        display: flex;
        list-style-type: none; }
    &__item {
        margin-right: 30px;
        font-size: 18px;
        &:last-child {
            margin-right: 0; } }
    &__link {
        text-decoration: none;
        color: #000000;
        transition: 0.3s all;
        &:hover {
            color: #F72464; } }
    &__cart {
        margin-left: auto;
        &-link {
            position: relative;
            display: inline-block;
            top: 5px;
            padding: 5px; }
        &-number {
            position: absolute;
            top: -5px;
            right: -5px;
            width: 20px;
            height: 20px;

            border-radius: 50%;
            font-size: 12px;
            font-weight: bold;
            line-height: 20px;
            text-align: center;

            color: #FFFFFF;
            background-color: #F72464; } } }

/* For mobile */
@media (max-width: 992px) {
    .nav {
        padding: 20px 0;
        &__logo {
            margin-right: 15px; }
        &__list {
            display: block;
            position: fixed;
            top: 0;
            left: -100%;
            bottom: 0;
            width: 260px;

            color: #FFFFFF;
            background-color: #000000;

            transition: left 0.4s ease-in;
            &-holder {
                z-index: 9;
                &--visible {
                    position: fixed;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;

                    background-color: rgba(0, 0, 0, 0.3);
                    transition: background-color 0.4s ease-in;
                    z-index: 9;
                    &, .nav__list {
                        left: 0; } } } }
        &__item {
            padding: 15px;
            margin: 0;
            border-bottom: 1px solid #313131; }
        &__link {
            display: block;
            color: inherit; }
        &__cart {
            margin-right: 15px; } } }

@media (max-width: 576px) {
    .nav {
        &__list {
            max-width: 77%; } } }
