.footer {
    padding: 30px 0;
    margin-top: 100px;
    color: #FFFFFF;
    background-color: #000000;
    &__column {
        display: flex;
        flex-direction: column;
        align-items: center;
        & > * + * {
            margin-top: 25px; } }
    &__nav {
        margin: 0 auto;
        font-size: 14px;
        text-transform: uppercase;
        & > &-item + &-item {
            margin-left: 28px; }
        &-item {
            text-decoration: none;
            color: #FFFFFF; } }
    &__socials {
        display: flex;
        align-items: center;
        & > &-link + &-link {
            margin-left: 31px; }
        &-link {
            text-decoration: none; } }
    &__copyright {
        font-size: 14px;
        color: #999999;
        & p {
            margin: 0; } } }

/* For mobile */
@media (max-width: 992px) {
    .footer {
        margin-top: 70px; } }

@media (max-width: 576px) {
    .footer {
        text-align: center;
        margin-top: 50px;
        &__nav {
            display: flex;
            flex-direction: column;
            & > &-item + &-item {
                margin-top: 15px;
                margin-left: 0; } } } }
